export const setMentorName = name => ({
    type: 'SET_MENTOR',
    payload: name
});

export const setTasks = tasks => ({
    type: 'SET_TASKS',
    payload: tasks
});

export const setData = data => ({
    type: 'SET_DATA',
    payload: data
});

export const setGitName = name => ({
    type: 'SET_GIT_NAME',
    payload: name
});
